body {
    font-family: 'Museo Sans Cyrl 300';
    letter-spacing: -0.24px;
    position: relative; }
header {
    height: 80px;
    width: 100%;
    position: fixed;
    z-index: 30;
    transition: opacity 0.5s ease-out;
    &::before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 9;
        width: 100%;
        height: 100px;
        transition: all .5s ease-out;
        background: linear-gradient(180deg,rgba(0,0,0,.7) -59.23%, rgba(0,0,0,0)); }
    .container {
        .header_logo {
            position: absolute;
            z-index: 35;
            display: block;
            left: 46.5%;
            background: url('../img/main_page/logo/logo_turan_white.svg') no-repeat 50%/cover;
            width: 106px;
            height: 54px;
            &:hover {
                background: url('../img/main_page/logo/logo_turan.svg') no-repeat 50%/cover; } } } }
nav {
    padding-top: 86px;
    padding-bottom: 50px;
    color: #fff;
    .container {
        padding: 0 15px;
        margin: 0 56.6px;
        ul {
            display: flex;
            letter-spacing: -0.24px;
            list-style: none;
            li {
                a {
                    text-decoration: none;
                    color: #fff;
                    margin-right: 25px; }
                .main_page {
                    z-index: 36;
                    opacity: 0.5;
                    position: relative;
                    &:hover {
                        opacity: 1.0; }
                    &::after {
                        content: "";
                        display: block;
                        width: 4px;
                        height: 4px;
                        border-radius: 50%;
                        background: #fff;
                        position: absolute;
                        left: 50px;
                        top: 8px;
                        // border: 1px solid #fff
                        margin: 0 12px; } } } } } }
.born {
    min-height: 100vh;
    img {
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        object-fit: cover; }
    .container {}
    &_content {
        padding: 113px 107px;
        &_title {
            color: #fff;
            font-size: 60px;
            margin-bottom: 30px;
            font-family: 'Montserrat Alternates'; }
        &_descr {
            margin-bottom: 20px;
            font-size: 16px;
            color: #ffffff; }
        &_subtitle {
            font-size: 150px;
            font-family: 'Montserrat Alternates';
            color: rgba(0,0,0,0);
            -webkit-text-stroke: 1px #fff; } }
    &_wave {
        background: url(' https://turanwater.com/assets/img/bottom-wave.svg') repeat-x 0 100%/contain;
        position: absolute;
        top: 732px;
        width: 100%;
        height: 18px; } }
.origin {
    padding: 0 0 80px 0;
    .container {
        padding: 0 15px;
        margin: 0 76px; }
    &_title {
        font-family: 'Montserrat Alternates';
        font-size: 66px;
        margin-bottom: 64px;
        color: #002856;
        span {
            color: #00a0df; } }
    &_text {
        max-width: 385px;
        margin-left: auto;
        margin-top: 110px;
        line-height: 29px; }
    &_mountain {
        max-width: 755px;
        height: 395px;
        background: url('https://turanwater.com/assets/img/mount.svg') no-repeat center top 25%/ contain;
        margin: -25px auto 0;
        position: relative;
        &_depth {
            font-family: 'Museo Sans Cyrl 500';
            display: flex;
            flex-direction: column;
            span {
                font-size: 50px;
                font-family: 'Montserrat Alternates';
                color: rgba(0, 0, 0, 0);
                -webkit-text-stroke: 1px #00a0df; } }
        &_temp {
            display: flex;
            flex-direction: column;
            position: absolute;
            font-family: 'Museo Sans Cyrl 500';
            right: 60px;
            bottom: 0;
            margin-left: auto;
            color: #182438;
            span {
                font-size: 50px;
                font-family: 'Montserrat Alternates';
                color: rgba(0, 0, 0, 0);
                -webkit-text-stroke: 1px #00a0df; } } } }

